import { Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { BsModalService } from 'ngx-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from '../service/crud.service';
import { AddSubInitiativeComponent } from '../strategic-plan/modals/add-sub-initiative/add-sub-initiative.component';
import { CreateMandateComponent } from '../strategic-plan/modals/mandates/create-mandate/create-mandate.component';
import { EditMandateComponent } from '../strategic-plan/modals/mandates/edit-mandate/edit-mandate.component';
import { ViewSubInitiativeComponent } from '../strategic-plan/modals/view-sub-initiative/view-sub-initiative.component';
declare var $: any;
@Component({
  selector: 'app-deliverable-management',
  templateUrl: './deliverable-management.component.html',
  styleUrls: ['./deliverable-management.component.scss']
})
export class DeliverableManagementComponent implements OnInit {
  mandates
  constructor(private modalService: BsModalService, private crud: CrudService, private toastr: ToastrService) { }
  
  ngOnInit(): void {
    this.getMandates()
  }

  createMandates() {
    const modalRef = this.modalService.show(CreateMandateComponent, { class: ''});
    modalRef.content.onClose.subscribe(result => {
      this.getMandates();
    })
  }

  editMandate(data) {
    const initialState = {
      data: data
    }
    const modalRef = this.modalService.show(EditMandateComponent, { class: '', initialState });
    modalRef.content.onClose.subscribe(result => {
      this.getMandates();
    })
  }
  
  openAddSubInitiative(mandate) {
    const initialState = {
      data: mandate
    };
    const modalRef = this.modalService.show(AddSubInitiativeComponent, { initialState });
    modalRef.content.onClose.subscribe(result => {
      this.getMandates()
    });
  }

  openViewSubInitiative(mandate) {
    const initialState = {
      data: mandate
    };
    const modalRef = this.modalService.show(ViewSubInitiativeComponent, { class: 'modal-lg', initialState });
  }
  
  getMandates(){
    const data = gql`
      {
        mandates{
          id
          title
          si
          mda{
            shortcode
          }
          projectsTotal
          subTotal
          projects{
            title
            budget
            status
          }
          outputs{
            title
            baseline
            actual
            target
          }
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.mandates = result.data.mandates;
        console.log(this.mandates);
        setInterval(() => {
        	$('.mandates').DataTable();
        }, 2000);
      });
  }

  deleteMandate(id) {

    //console.log(id);

    let data = gql`
      query deleteMandate($id:ID!) {
        deleteMandate( id:$id) {
          id
          }
        }
        `
    this.crud.pullData(data, { id: id })
      .subscribe(result => {
        if (!result.data['deleteMandate']) {
          this.toastr.error("Oops, something went wrong, kindly try again", "Unsuccessful")
        } else {
          this.getMandates()
          this.toastr.success("Deliverable Deleted", "Successful")
        }
      });
  }

  getSubinitiative(){}
}
